"use client";
// React and Next
import { useCallback, useEffect, useState } from "react";
import Link from "next/link";

// Utils
import { imageUrl } from "@/utilities/url";
import { PriceTaxCalculate } from "@/utilities/PriceCalculate";

// 3rd Party
import moment from "moment";

// API
import { MyDownloadsDetailApi } from "@/api/clientsideaction/productsearch";
import { GetAddonList } from "@/utilities/localStorage";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";

// Components
import Sidebar from "@/Components/Myprofile/sidebar";
import Breadcrumb from "@/Components/Breadcrumbs";
import OrderDetailsSkeleton from "./MyDownloadsDetailSkeleton";
import { useTranslation } from "react-i18next";

const MyDownloadsDetailPage = ({ id }) => {
  // Language Switcher
  const { t } = useTranslation();

  // Detail api response
  const [detailData, setDetailData] = useState({});

  // Loader
  const [loader, setLoader] = useState(false);

  // Addonlist
  const addonList = GetAddonList();

  // Breadcrumbs
  const orderBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.orderHistory"),
      url: "/account-settings/order-history",
    },
    {
      text: t("Breadcrumb.orderDetail"),
      url: "javascript:void(0)",
    },
  ];

  // Order Detail Api
  const myDownloadsDetailPageApi = useCallback(async () => {
    setLoader(true);
    const data = {
      orderProductId: id,
    };
    const response = await MyDownloadsDetailApi(id, data);
    if (response?.status == 1) {
      setDetailData(response?.data);
      setLoader(false);
    }
  }, []);

  useEffect(() => {
    myDownloadsDetailPageApi();
  }, [myDownloadsDetailPageApi]);
  return (
    <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
      <Breadcrumb breadCrumb={orderBreadCrumbEdit} />

      <div className="flex gap-8 mx-auto mt-4 mb-7">
        <Sidebar />
        {loader ? (
          <OrderDetailsSkeleton />
        ) : (
          <div className="w-full">
            <div className="flex justify-between items-end space-x-3 rtl:space-x-[unset] mb-6 pb-4 border-slate-200 border-b">
              <div className="flex flex-col space-y-1 rtl:ml-3">
                <h3 className="font-medium text-[28px] text-black-300">
                  {t("orderhistory.OrdDtl")}
                </h3>
                <p className="font-normal text-black-300 text-sm">
                  {t("orderhistory.OrdOn")}{" "}
                  {moment(detailData?.orderedDate)?.format(
                    "DD MMM, YYYY, hh:mm A"
                  )}{" "}
                  |{t("orderhistory.Order")} #{detailData?.orderProductPrefixId}{" "}
                  {detailData?.priceGroupDetailId == 0
                    ? null
                    : `| ${t("orderhistory.PrsnlzdOrd")} `}
                </p>
              </div>
            </div>
            <div className="flex flex-wrap justify-between items-start gap-4 mb-4 px-4">
              <div className="flex flex-col space-y-4">
                <h3 className="font-medium text-black-300 text-base">
                  {" "}
                  {t("myprofile.BillAddr")}
                </h3>
                <div className="flex flex-col space-y-1">
                  <p className="font-normal text-black-300 text-sm">
                    {detailData?.paymentFirstname} {detailData?.paymentLastname}
                    {" , "}
                  </p>
                  <p className="font-normal text-black-300 text-sm">
                    {detailData?.paymentAddress1}{" "}
                  </p>

                  <p className="font-normal text-black-300 text-sm">
                    {detailData?.paymentAddress2}{" "}
                    {detailData?.paymentCity && ","}
                  </p>

                  <p className="font-normal text-black-300 text-sm">
                    {detailData?.paymentCity}{" "}
                  </p>

                  <p className="font-normal text-black-300 text-sm">
                    {detailData?.paymentMobileNumber}{" "}
                    {detailData?.paymentPostcode && ","}
                  </p>

                  <p className="font-normal text-black-300 text-sm">
                    {detailData?.paymentPostcode}
                  </p>
                </div>
              </div>
              {detailData?.paymentMethod && (
                <div className="flex flex-col space-y-4">
                  <h3 className="font-mediuml text-black-300 text-base">
                    {" "}
                    {t("orderhistory.PmtMthd")}
                  </h3>
                  <div className="flex flex-col space-y-1">
                    <p className="font-normal text-black-300 text-sm">
                      {detailData?.paymentMethod}
                    </p>
                  </div>
                </div>
              )}

              <div className="flex flex-col space-y-4 max-w-auto">
                <h3 className="font-medium text-black-300 text-base">
                  {t("orderhistory.OrdSum")}
                </h3>
                <div className="flex flex-col space-y-2">
                  <div className="justify-between items-center gap-10 grid grid-cols-2">
                    <h5 className="font-medium text-black-300 text-sm">
                      {t("orderhistory.Quantity")}:

                    </h5>
                    <p className="font-normal text-black-300 text-sm text-right">
                      {detailData?.productQuantity}
                    </p>
                  </div>
                  <div className="justify-between items-center gap-10 grid grid-cols-2">
                    <h5 className="font-medium text-black-300 text-sm">
                      {t("orderhistory.BasePrice")}:
                    </h5>
                    <p className="font-normal text-black-300 text-sm text-right">
                      <CustomCurrencyFormatter
                        price={
                          detailData?.basePrice * detailData?.productQuantity
                        }
                      />
                    </p>
                  </div>
                  <div className="justify-between items-center gap-10 grid grid-cols-2">
                    <h5 className="font-medium text-black-300 text-sm">
                      {t("orderhistory.Tax")}:
                    </h5>
                    <p className="font-normal text-black-300 text-sm text-right">
                      <CustomCurrencyFormatter
                        price={
                          detailData?.taxType == 1
                            ? detailData?.taxValue * detailData?.productQuantity
                            : (detailData?.basePrice -
                                detailData?.discountAmount) *
                              (Number(detailData?.taxValue) / 100) *
                              detailData?.productQuantity
                        }
                      />
                    </p>
                  </div>

                  <div className="justify-between items-center gap-10 grid grid-cols-2">
                    <h5 className="font-medium text-black-300 text-sm">
                      {t("orderhistory.Discount")}:
                    </h5>
                    <p className="font-normal text-black-300 text-sm text-right">
                      <CustomCurrencyFormatter
                        price={
                          detailData?.discountAmount *
                          detailData?.productQuantity
                        }
                      />
                    </p>
                  </div>

                  <div className="justify-between items-center gap-10 grid grid-cols-2">
                    <h5 className="font-medium text-black-300 text-sm">
                      {t("orderhistory.CpnDisc")}:
                    </h5>
                    <p className="font-normal text-black-300 text-sm text-right">
                      <CustomCurrencyFormatter
                        price={detailData?.couponDiscountAmount}
                      />
                    </p>
                  </div>
                  <div className="justify-between items-center gap-10 grid grid-cols-2">
                    <h5 className="font-medium text-black-300 text-sm">
                      {t("orderhistory.GndTot")}:
                    </h5>
                    <p className="font-normal text-black-300 text-sm text-right">
                      <CustomCurrencyFormatter price={detailData?.total} />
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div className="px-0">
              <div className="p-4 border border-slate-100">
                <div className="flex flex-wrap justify-between items-start gap-4">
                  <div className="flex items-start space-x-4 rtl:space-x-[unset]">
                    <div className="rtl:ml-4 w-[100px] min-w-[100px] h-[100px]">
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          detailData?.containerName +
                          "&name=" +
                          detailData?.productImage +
                          "&width=400&height=200"
                        }
                        alt={detailData?.productName}
                        onError={({ currentTarget }) => {
                          currentTarget.onerror = null;
                          currentTarget.src = "/img/no-image.png";
                          currentTarget.alt = "no-image";
                        }}
                        className="m-auto w-auto max-w-full h-full"
                      />
                    </div>
                    <div>
                      <h6 className="font-medium text-black-300 text-sm">
                        {detailData?.productNameTrans
                          ? detailData?.productNameTrans
                          : detailData?.productName}
                      </h6>
                      <p className="font-normal text-black-300 text-sm">
                        <CustomCurrencyFormatter
                          price={detailData?.productPrice}
                        />
                      </p>
                      {Number(detailData?.discountedAmount) !== 0 && (
                        <div className="flex items-center space-x-2 rtl:space-x-[unset]">
                          <p className="rtl:ml-2 font-normal text-black-300 text-sm">
                            {t("productdetails.Mrp")}{" "}
                            <del>
                              <CustomCurrencyFormatter
                                price={PriceTaxCalculate(
                                  detailData?.basePrice,
                                  detailData?.taxType,
                                  detailData?.taxValue
                                )}
                              />
                            </del>
                          </p>
                          <span className="font-normal text-activegreen text-sm">
                            {Math.round(
                              ((PriceTaxCalculate(
                                detailData?.basePrice,
                                detailData?.taxType,
                                detailData?.taxValue
                              ) -
                                PriceTaxCalculate(
                                  detailData?.discountedAmount,
                                  detailData?.taxType,
                                  detailData?.taxValue
                                )) /
                                PriceTaxCalculate(
                                  detailData?.basePrice,
                                  detailData?.taxType,
                                  detailData?.taxValue
                                )) *
                                100
                            )}
                            {t("productdetails.OFF")}
                          </span>
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="flex flex-row lg:flex-col flex-wrap justify-center space-x-3 lg:space-x-0 lg:space-y-3 w-full lg:w-auto">
                    {addonList &&
                      addonList?.["rating-review"] &&
                      addonList?.["rating-review"]?.status == 1 && (
                        <Link
                          prefetch={false}
                          href={`/account-settings/order-history/review/${detailData?.orderProductId}`}
                          className="flex justify-center items-center hover:bg-slate-50 px-4 border border-boldblack rounded max-[380px]:w-full h-10 font-normal text-boldblack"
                        >
                          {t("orderhistory.RevProd")}
                        </Link>
                      )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default MyDownloadsDetailPage;
